import * as THREE from 'three'

class Environment 
{
    constructor(experience)
    {
        this.experience = experience
        this.scene = this.experience.scene
        this.resources = this.experience.resources

        this.setEnvironment()
        this.setEnvironmentMap()
    }

    // Create lights
    setEnvironment()
    {
        this.directional = new THREE.DirectionalLight(0xff00ff , 0)
        this.directional.castShadow = true
        this.directional.shadow.camera.far = 15
        this.directional.shadow.mapSize.set(1024,1024)
        this.directional.shadow.normalBias = 0.05
        this.directional.position.set(0, 1, 2)

        this.point = new THREE.PointLight(0xff00ff , 0)
        this.point.castShadow = true
        this.point.position.set(2, 1, 0)

        this.point2 = new THREE.PointLight(0xffffff , 0)
        this.point2.castShadow = true
        this.point2.position.set(-2, 2, 0)

        this.point3 = new THREE.PointLight(0x0000ff , 5)
        // this.point3.castShadow = true
        this.point3.position.set(0, 1.3, 0)


        this.ambient = new THREE.AmbientLight(0xffffff, 0)

        // this.experience.debug.gui.add(this.point3.position, 'x', 0, 5, 0.001)
        // this.experience.debug.gui.add(this.point3.position, 'y', 0, 5, 0.001)
        // this.experience.debug.gui.add(this.point3.position, 'z', 0, 5, 0.001)

        this.scene.add(this.point3, this.ambient, this.directional, this.point2, this.point)

    }

    // Create environment map
    setEnvironmentMap()
    {
        this.environmentMap = {}
        this.environmentMap.texture = this.resources.items.environmentMapTexture
        this.environmentMap.texture.encoding = THREE.sRGBEncoding

        this.scene.environment = this.environmentMap.texture

        this.environmentMap.updateMaterials = () =>
        {
            this.scene.traverse((child) =>
            {
                if(child instanceof THREE.Mesh && child.material instanceof THREE.MeshStandardMaterial)
                {
                    child.material.metalness = 0.8
                    child.material.roughness = 0.1
                    child.material.envMap = this.environmentMap.texture
                    child.material.envMapIntensity = 0.7
                    child.material.needsUpdate = true
                }
            })
        }
        this.environmentMap.updateMaterials()
    }
}

export default Environment