import * as THREE from 'three'
import gsap from 'gsap'

class CameraAngles
{
    constructor(experience)
    {
        this.experience = experience

        this.partPicked = 'yellow_WhiteCar_0'
        this.buttons = document.querySelectorAll('.parts-button')

        this.color = new THREE.Color(0x0000ff)
        this.colorButtons = document.querySelectorAll('.color-pick')

        this.changeCamera()
        this.createColorClicks()

        if(this.experience.world.model)
        {
        }
    }

    changeCamera()
    {
        for(const button of this.buttons)
        {
            button.addEventListener('click',(e)=>
            {
                if(e.currentTarget.id === "body")
                {
                    gsap.to(this.experience.camera.instance.position, {duration: 0.5, x:0, y:1, z:3})
                    this.partPicked = 'yellow_WhiteCar_0'
                }
                else if(e.currentTarget.id === "windows")
                {
                    gsap.to(this.experience.camera.instance.position, {duration: 0.5, x:-1, y:0.65, z:-0.3})
                    this.partPicked = 'glass_003_Glass_0'
                }
                else if(e.currentTarget.id === "wheels")
                {
                    gsap.to(this.experience.camera.instance.position, {duration: 0.5, x:1.4, y:-0.3, z:-1.31})
                    this.partPicked = 'wheel003_018_3_RimsChrome_0'
                }
                else
                {
                    gsap.to(this.experience.camera.instance.position, {duration: 0.5, x:-1.3, y:-0.3, z:1.5})
                    this.partPicked = 'silver_001_BreakDiscs_0'
                }
            })
        }
    }

    createColorClicks()
    {
        for(const c of this.colorButtons)
        {
            c.addEventListener('click',()=>
            {
                gsap.to(this.experience.renderer.rgbShiftPass.uniforms.amount, {duration:0.2, value: 0.02})
                gsap.to(this.experience.renderer.rgbShiftPass.uniforms.amount, {delay:0.2, duration:0.2, value: 0})
                
                if(c.id === 'white')
                {
                    this.color = new THREE.Color(0xffffff)
                }
                else if (c.id === 'black')
                {
                    this.color = new THREE.Color(0x000000)
                }
                else if(c.id === 'red')
                {
                    this.color = new THREE.Color(0xeff0008)
                }
                else if (c.id === 'green')
                {
                    this.color = new THREE.Color(0x4ff00)
                }
                else if(c.id === 'blue')
                {
                    this.color = new THREE.Color(0x0000ff)
                }
                else if(c.id === 'yellow')
                {
                    this.color = new THREE.Color(0xffaa00)
                }
                else if(c.id === 'purple')
                {
                    this.color = new THREE.Color(0xf700ff)
                }

                this.experience.world.model.model.traverse((child)=>
                {
                    if(child instanceof THREE.Mesh)
                    {
                        if(child.name === this.partPicked)
                            {
                            child.material.color = new THREE.Color(this.color)
                        }
                    }
                })
            })
        }
    }


}

export default CameraAngles