import * as THREE from 'three'
import Experience from './Experience.js'
import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls.js'

export default class Camera
{
    constructor()
    {
        this.experience = new Experience()
        this.sizes = this.experience.sizes
        this.scene = this.experience.scene
        this.canvas = this.experience.canvas

        this.setInstance()
        this.setControls()
    }

    setInstance()
    {
        this.instance = new THREE.PerspectiveCamera(65, this.sizes.width / this.sizes.height, 0.1, 100)
        this.instance.position.set(2.5, 0.15, -1.3)
        this.scene.add(this.instance)

        // this.experience.debug.gui.add(this.instance.position, 'x', -5, 5, 0.001)
        // this.experience.debug.gui.add(this.instance.position, 'y', -5, 5, 0.001)
        // this.experience.debug.gui.add(this.instance.position, 'z', -5, 5, 0.001)

    }

    setControls()
    {
        this.controls = new OrbitControls(this.instance, this.canvas)
        this.controls.enableDamping = true
        this.controls.maxPolarAngle = 1.7
        this.controls.minPolarAngle = -1
        this.controls.maxDistance = 3
    }

    resize()
    {
        this.instance.aspect = this.sizes.width / this.sizes.height
        this.instance.updateProjectionMatrix()
    }

    update()
    {
        this.controls.update()
    }
}