import * as THREE from 'three'
import {Text} from 'troika-three-text'

class Font
{
    constructor(experience)
    {
        this.experience = experience

        this.createHeader()
    }

    createHeader()
    {
        this.header = new Text()       

        this.header.text = `URUS`
        this.header.textAlign = 'center'
        this.header.anchorX = 'center'
        this.header.anchorY = '60%'
        this.header.fontSize = 2
        this.header.outlineWidth = '2%'
        this.header.outlineColor = new THREE.Color(0x333333)
        this.header.color = new THREE.Color(0x333333)
        this.header.rotation.y = Math.PI / 2
        this.header.position.set(-3.5,-0.07,0)
        this.header.sync()

        // this.experience.scene.add(this.header)
    }

    update()
    {
    }
}

export default Font
