import * as THREE from 'three'
import {Reflector} from 'three/examples/jsm/objects/Reflector.js' 

class Floor
{
    constructor(experience)
    {
        this.experience = experience

        this.createFloor()
    }

    createFloor()
    {
        this.geo = new THREE.CircleBufferGeometry(8,64,32)

        // this.floor = new Reflector( this.geo, {
        //     map:this.experience.resources.items.colorTexture,
        //     // normalMap: this.experience.resources.items.normalTexture,
        //     // aoMap: this.experience.resources.items.aoTexture,
        //     clipBias: 0.003,
        //     textureWidth: window.innerWidth * window.devicePixelRatio,
        //     textureHeight: window.innerHeight * window.devicePixelRatio,
        //     color: 0x111111,
        // } );

        this.floor = new THREE.Mesh(
            this.geo,
            new THREE.MeshStandardMaterial({
                map:this.experience.resources.items.colorTexture,
                normalMap: this.experience.resources.items.normalTexture,
                aoMap: this.experience.resources.items.aoTexture,
                color:0x000000
            })
        )

        
        this.floor.rotation.x = -Math.PI / 2
        this.floor.position.y = -0.5
        this.floor.receiveShadow = true
        this.experience.scene.add(this.floor)

        // this.experience.debug.gui.add(this.floor.material, 'blendDst', 0, 500, 0.1).
    }
}

export default Floor