import * as THREE from 'three'
import gsap from 'gsap'

class Model
{
    constructor(experience)
    {
        this.experience = experience

        this.resource = this.experience.resources.items.bagModel
        this.lightsResource = this.experience.resources.items.lightsModel

        this.createModels()
        // this.createLights()
    }
    
    // Add model to scene
    createModels()
    {
        this.model = this.resource.scene
        this.model.scale.set(0.007,0.007,0.007)
        this.model.position.y = 0.03

        this.model.traverse((child)=>
        {
            if(child instanceof THREE.Mesh)
            {
                child.castShadow = true
                child.material.envMapIntensity = 1.4
            }
        })


        this.experience.scene.add(this.model)
    }

    createLights()
    {
        this.lightsModel = this.lightsResource.scene
        this.lightsModel.scale.set(0.005,0.005,0.005)
        this.lightsModel.position.set(0, 2, 0)
        // this.model.position.y = 0.03

        this.lightsModel.traverse((child)=>
        {
            if(child instanceof THREE.Mesh)
            {
                child.castShadow = true
            }
        })

        this.experience.debug.gui.add(this.lightsModel.position, 'x', -5, 5, 0.001)
        this.experience.debug.gui.add(this.lightsModel.position, 'y', -5, 5, 0.001)
        this.experience.debug.gui.add(this.lightsModel.position, 'z', -5, 5, 0.001)


        this.experience.scene.add(this.lightsModel)
    }
}

export default Model