import * as THREE from 'three'
import gsap from 'gsap'

import CameraAngles from '../Functions/CameraAngles.js'
import ColorPick from '../Functions/ColorPick.js'
import Model from '../World/Model.js'
import Background from './Background.js'
import Environment from './Environment.js'
import Floor from './Floor.js'
import Font from './Font.js'

class World
{
    constructor(experience)
    {
        this.experience = experience
        this.scene = this.experience.scene
        this.resources = this.experience.resources

        this.resources.on('loaded' , ()=>{
            this.model = new Model(this.experience)
            this.environment = new Environment(this.experience)
            this.floor = new Floor(this.experience)
            this.background = new Background(this.experience)
            // this.colorPicker = new ColorPick(this.experience)
            this.cameraAngles = new CameraAngles(this.experience)
            this.font = new Font(this.experience)
            // this.createRings()
            this.onLoadAnimations()
        })
    }

    onLoadAnimations()
    {
        gsap.to(this.environment.point, {duration:2, intensity: 4})
        gsap.to(this.environment.ambient, {duration:2, intensity: 1})
        gsap.to(this.environment.point3, {duration:2, intensity: 4})
        gsap.to(this.environment.directional, {duration:2, intensity: 4})
    }

    createRings()
    {
        this.ring = new THREE.Mesh(
            new THREE.RingBufferGeometry(3,3.1,64,32),
            new THREE.MeshBasicMaterial({color:0x6666ff, side:THREE.DoubleSide})
        )
        this.ring.position.y = -0.5

        this.ring2 = new THREE.Mesh(
            new THREE.RingBufferGeometry(2.5,2.51,64,32),
            new THREE.MeshBasicMaterial({color:0xff66ff, side:THREE.DoubleSide})
        )
        this.ring2.position.set(0, -0.5, -3)
        
        this.ring3 = new THREE.Mesh(
            new THREE.RingBufferGeometry(2.5,2.51,64,32),
            new THREE.MeshBasicMaterial({color:0xff66ff, side:THREE.DoubleSide})
        )
        this.ring3.position.set(0, -0.5, 3)

        this.scene.add(this.ring, this.ring2, this.ring3)
    }

    update()
    {

    }
}

export default World